
import { PropType } from 'vue'
import { useStore } from 'vuex'
import { IFeatureResultItem } from '@/api/user'

export default {
  props: {
    features: {
      type: Array as PropType<Array<IFeatureResultItem>>
    }
  },
  setup() {
    const store = useStore()
    const userInfo = store.getters.userInfo
    return {
      userInfo
    }
  }
}
